.listitem-img{
    padding: 5px;
    width: 110px;
    height: 110px;
    float: left;
}

.listitem-div{
    padding: 5px;
    padding-top: 25px;
    width: 236px;
    height: 110px;
    /* background-color: aqua; */
    float: left;
}
.listitem-span-three{
    padding-top: 10px;
    width: 236px;
}
.listitem-span-one{
    margin-top: 11.5px;
    height: 37px;
    position: relative;
    overflow: hidden;
    line-height: 18.5px;
    text-overflow: ellipsis;
    min-height: 0;
    font-size: 22px;
    color: #333333;
    font-family: "Microsoft YaHei";
}

.listitem-span-four{
    /* margin-top: 15px; */
    font-size: 11px;
    color: red;
}
.listitem-span-two{
    /* margin-top: 15px; */
    font-size: 14px;
    line-height: 16px;
    color: #ff5500;
}

.listitem-btn-one{
    width: 120px;
    height: 46px;
    background: #FF9500;
    border: none;
    font-size: 15px;
    line-height: 34px;
    margin-top: 0px;
    color: white;
    width: 112px;
    border-bottom-left-radius: 15px;
    border-top-left-radius: 15px;
}

.listitem-btn-two{
    width: 120px;
    height: 46px;
    border: none;
    background-color: #FF0036;
    font-size: 15px;
    line-height: 34px;
    margin-top: 0px;
    color: white;
    width: 112px;
    border-bottom-right-radius: 15px;
    border-top-right-radius: 15px;
}