.doc .am-grid .am-flexbox .am-flexbox-item .am-grid-item-content{
    padding: 0px;
}

.doc .mygridimg{
    width: 100%;
    text-align: center;
    padding: 12px 0px;
    margin: 0px;
}

.doc .mygridimg img{
    width: 161px;
    height: 19px;
}

.dataitem-bottom{
    width: 100%;
    height: 50px;
}

.dataitem{
    width: 100%;
    height: 100%;
}

.dataitem-img{
    width: 171px;
    height: 171px;
    background-color: red;
}

.dataitem-div-one{
    margin-top: 11.5px;
    height: 37px;
    position: relative;
    overflow: hidden;
    line-height: 18.5px;
    text-overflow: ellipsis;
    min-height: 0;
    font-size: 13px;
    color: #333333;
    font-family: "Microsoft YaHei";
}

.dataitem-span-one{
    font-size: 11px;
    color: red;
}

.dataitem-span-two{
    font-size: 14px;
    line-height: 16px;
    color: #ff5500;
}

.dataitem-span-three{
    font-size: 11px;
    color: #999999;
    margin-top: 2.0000000000000004px;
}

span[class ^="dataitem-span"]{
    margin: 0px 2px;
    font-family: "Microsoft YaHei";
}
.dataitm-div-two{
    flex-direction: row;
    margin-top: 11px;
    height: 20px;
    line-height: 20px;
}