.doc .am-search{
    background-color: rgb(255, 133, 42);
    width: 324.33px;
    height: 25px;
    padding: 6px 9px 6px 42px;
    background-image: url(data:image/png;base64,);
    background-repeat: no-repeat;
    background-size: 27px 20px;
    background-position: 9px center;
}
.doc .am-search-input{
    width: 324.33px;
    height: 25px;
    background-color: rgb(255, 78, 34);
}
.doc .am-search-input .am-search-synthetic-ph-placeholder{
    color: rgb(240, 224, 220);
    font-size: 13px;
    font-weight: bold;
}

.doc .am-search-cancel{
    display: none;
}

.doc .am-wingblank.am-wingblank-lg{
    margin: 0px;
    touch-action: none;
}


div .am-tab-bar { 
    position: fixed;
    bottom: 0;
    width: 100%;
    height:70px;
}

.doc .am-carousel-wrap-dot-active > span{
    background-color: rgb(255, 78, 34);
}