.shangpin-image img{
    width: 100%;
}
.shangpin-image{
    position: relative;
}
.shangpin-button-one{
    font-size: 20px;
    color: #fff;
    position: absolute;
    height: 30px;
    width: 30px;
    margin: 5px;
    text-align: center;
    fill: currentColor;
    position: absolute;
    top: 0px;
    left: 0px;
    z-index: 1;
    background-color: rgba(0,0,0,.4);
    border-radius: 100%;
}

.am-tab-bar-tab{
   padding-top: 4px;
    width: 50px;
    height: 46px;
}


.am-tab-bar-tab:nth-child(1){
    width: 50px;
    height: 46px;
    color: #949494;
}
.am-tab-bar-tab:nth-child(2){
    width: 50px;
    height: 46px;
    color: #949494;
}
.am-tab-bar-tab:nth-child(3){
    width: 50px;
    height: 46px;
    color: #949494;
}
.shangpin .am-tab-bar-tab:nth-child(4){
    width: 112px;
    height: 46px;
    background: #FF9500;
}
.am-tab-bar-tab:nth-child(5){
    width: 112px;
    height: 46px;
    
    background-color: #FF0036;
}

.shangpin .am-tab-bar-tab:nth-child(4) .am-tab-bar-tab-image{
    display: none;
}

.am-tab-bar-tab:nth-child(5) .am-tab-bar-tab-image{
    display: none;
}

.shangpin .am-tab-bar-tab:nth-child(4) .am-tab-bar-tab-title{
    font-size: 15px;
    line-height: 34px;
    margin-top: 0px;
    color: white;
    width: 112px;
}

.am-tab-bar-tab:nth-child(5) .am-tab-bar-tab-title{
    font-size: 15px;
    line-height: 34px;
    margin-top: 0px;
    color: white;
    width: 112px;
}